
#app {
  font-family:'Microsoft Yahei', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  font-size: 14px;
  /*margin-top: 60px;*/
}

html,
body,
div,
span,
ul,
li,
a,
b,
i,
em,
strong {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: none;
}
a {
  text-decoration: none;
}
.logo {
  position: absolute;
  left: 20px;
  color: #fff;
  z-index: 100;
  height: 75px;
  line-height: 75px;
  background-color: #ccc;
  padding: 0 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 24px;
}
.login {
  position: absolute;
  z-index: 100;
  right: 0;
  height: 30px;
  line-height: 30px;
  padding: 0 20px;
  color: green;
}
.nav {
  background-color: #343A46;
  position: relative;
  text-align: right;
  padding: 30px 0 10px 0;
}
.nav > li {
  display: inline-block;
  border-left: 1px solid #3E444F;
  border-right: 1px solid #292E37;
  margin-left: -3px;
  position: relative;
}
.nav > li:after {
  position: absolute;
  content: '';
  height: 20px;
  left: 0;
  right: 0;
}
.nav > li > a {
  display: block;
  padding: 8px 25px;
  color: #fff;
  position: relative;
}
.nav > li > a b {
  display: inline-block;
  height: 4px;
  width: 4px;
  border-width: 2px 2px 0 0;
  border-color: #C8C8CD;
  border-style: solid;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  position: absolute;
  top: 50%;
  right: 15%;
  margin-top: -4px;
}
.toolmenu {
  position: absolute;
  top: 75px;
  left: 0;
  right: 0;
  padding: 20px 30px;
  background-color: #424e66;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.toolmenu .menu {
  color: #eee;
  padding: 10px 20px;
  font-size: 12px;
}
.toolmenu .menu li {
  margin: 15px 0;
}
.toolmenu .menu li a {
  color: #fff;
}
.toolmenu .menu li a:hover {
  color: #58a;
}
.toolmenu .menu li:first-child {
  font-weight: bold;
  color: #fff;
  font-size: 14px;
}
.guidemenu {
  position: absolute;
  top: 45px;
  left: 0;
  right: 0;
  background-color: #424e66;
}
.guidemenu li:hover {
  background-color: #000;
}
.guidemenu a {
  display: block;
  color: #fff;
  padding: 10px;
  text-align: left;
  font-size: 12px;
}
.nav > li a:after {
  background-color: #02a3d8;
  bottom: 0px;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: all 0.1s ease 0s;
  transition: all 0.1s ease 0s;
  visibility: hidden;
  width: 100%;
}
.nav > li > a:hover b {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 60%;
}
.nav > li > a:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  visibility: visible;
}
